.cruises {
  background-color: $lightcol;
  color: $darkcol;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  text-align: center;
  padding-top: 100px;
}

.cruises h2 {
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 28px;
}

.cruises__list {
  margin-left: auto;
  margin-right: auto;
  max-width: 1366px;
  list-style: none;
  padding-left: 0;
  margin-bottom: 90px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.cruises__item {
  margin-right: 30px;
  margin-bottom: 30px;
  position: relative;
}

.cruises__item:nth-child(4n) {
  margin-right: 0;
}

.cruises__item:nth-last-child(1) {
  margin-right: 0;
}

.cruises__image::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(180deg, rgba(1, 28, 64, 0) 50%, $darkcol 100%);
}

.cruises__item > p {
  position: absolute;
  top: 323px;
  left: 30px;
  font-weight: 500;
  font-size: 18px;
  color: $lightcol;
}

.cruises__image {
  width: 260px;
  height: 386px;
  overflow: hidden;
}


.cruises__info {
  visibility: hidden;
  width: 100%;
  min-height: 100%;
  padding-bottom: 80px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: $darkcol;
  color: $lightcol;
  text-align: left;
  padding-left: 25px;
}

.cruises__item:hover .cruises__info,
.cruises__item:focus-within .cruises__info,
.cruises__item:active .cruises__info {
  visibility: visible;
}

.cruises__info h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  margin-top: 30px;
  margin-bottom: 15px;
}

.cruises__info-list {
  list-style: none;
}

.cruises__info-term {
  font-weight: 500;
  margin-top: 15px;
  margin-bottom: 0;
}

.cruises__info-defenition {
  margin-left: 0;
  margin-top: 0;
}

.cruises__button {
  @include button;
  display: block;
  padding-top: 11px;
  position: absolute;
  bottom: 35px;
  width: 210px;
  height: 40px;
  margin-top: 10px;
}

.cruises__button:hover,
.cruises__button:focus,
.cruises__button:active {
  opacity: 0.8;
}

@media (max-width: 1129px) {
  .cruises__item:nth-child(4n) {
    margin-right: 30px;
  }

  .cruises__item:nth-child(3n) {
    margin-right: 0;
  }

  .cruises__item:nth-last-child(1) {
    margin-right: 0;
  }

  .cruises__info h3 {
    margin-bottom: -3px;
  }
}

@media (max-width: $tablet-width) {
  .cruises {
    padding-top: 80px;
  }

  .cruises h2 {
    font-size: 26px;
  }

  .cruises__image {
    width: 309px;
    height: 386px;
  }

  .cruises__image img {
    width: 309px;
    height: 386px;
  }

  .cruises__item:nth-child(3n) {
    margin-right: 30px;
  }

  .cruises__item:nth-last-child(1) {
    margin-right: 0;
  }

  .cruises__list {
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
    padding-bottom: 67px;
  }

  .cruises__item:nth-child(2n) {
    margin-right: 0;
  }

  .cruises__item > p {
    top: 320px;
    left: 35px;
  }

  .cruises__info {
    padding-left: 35px;
    padding-right: 35px;
  }

  .cruises__button {
    width: 240px;
  }
}

@media (max-width: $mobile-width) {
  .cruises {
    padding-top: 44px;
  }

  .cruises__list {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    padding-bottom: 35px;
  }

  .cruises h2 {
    font-size: 24px;
    margin-bottom: 40px;
  }

  .cruises__image {
    width: 270px;
  }

  .cruises__image img {
    width: 270px;
  }

  .cruises__item:nth-child(3n) {
    margin-right: 0;
  }

  .cruises__item {
    margin-right: 0;
    max-width: 270px;
  }

  .cruises__info {
    padding-left: 25px;
    padding-right: 25px;
  }

  .cruises__item > p {
    top: 327px;
    left: 25px;
  }

  .cruises__button {
    width: 220px;
  }
}
